.content {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
  }

  .info {
    display: flex;

    @media screen and (min-width: 768px) {
      flex-direction: column;
      gap: 40px;
    }
  }

  .questions {
    align-self: center;
  }
}

