
@import 'Variables.scss';

.form-container {
  width: 100%;

  @media screen and (min-width: 768px) {
    width: 80%;
    max-width: 300px;
    padding: 0;
  }
}

label {
  display: block;
  margin-bottom: 10px;
  margin-top: 24px;
}

input, textarea {
  width: 100%;
  height: 64px;
  margin-bottom: 5px;
  padding: 0 10px;
  border-radius: 5px;
  border: none;
  box-sizing: border-box;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}

textarea {
  height: 250px;
  padding: 20px 10px;
  resize: none;
}

input:focus, textarea:focus {
  outline: none;
}

.error {
  display: block;
  height: 7px;
  color: $error-color;
}

button {
  margin-top: 30px;
}

.logo {
  @media screen and (min-width: 768px) {
    width: 300px;
    height: 300px;
  }

  img {
    width: 100%;
    height: auto;
  }
}
